import { Component, Input } from '@angular/core';
import { OnatComponent } from './onat';

@Component({
  selector: 'onat-ge',
  templateUrl: './onat-ge.html',
  styleUrls: ['./onat.css']
})
export class OnatGEComponent{

    tpm:OnatComponent = new OnatComponent();
    name = this.tpm.name;
    title = this.tpm.title;
    message = this.tpm.message;
    highlight = this.tpm.highlight;
    languages = this.tpm.languages;
    interests = this.tpm.interests;
    badges = this.tpm.badges;
    schools = this.tpm.schools;
    references = this.tpm.references;

    works = this.tpm.works;
    works2 = this.tpm.works2;

    tech = this.tpm.tech;
    skills = this.tpm.skills;

  contact = [
    { "text": "onat.bas@ge.com", "disabled---image": "assets/mail.png" },
    { "text": "+1 647 797-0297 (CA)", "image": "assets/phone.png" },
    { "text": "+90 541 730-4804 (TR)", "image": "assets/phone.png" },
    { "text": "onatbas", "image": "assets/linkedin.png", "link": "https://linkedin.com/in/onatbas" },
    { "text": "onatbas", "image": "assets/github.png", "link": "https://github.build.ge.com/onatbas" },
    { "text": "TTC", "image": "assets/location.png" },
  ]

    public constructor (){
        this.works[0].fields[0] = this.works[0].fields[0].replace('17', '7');
    }
      
}